@-webkit-keyframes ripple-effect {
  0% {
    -webkit-transform: translate(50%, -50%) scale(0);
            transform: translate(50%, -50%) scale(0);
    opacity: 0; }
  50% {
    -webkit-transform: translate(50%, -50%) scale(2);
            transform: translate(50%, -50%) scale(2);
    opacity: 0.85; }
  100% {
    -webkit-transform: translate(50%, -50%) scale(4.5);
            transform: translate(50%, -50%) scale(4.5);
    opacity: 0; } }

@keyframes ripple-effect {
  0% {
    -webkit-transform: translate(50%, -50%) scale(0);
            transform: translate(50%, -50%) scale(0);
    opacity: 0; }
  50% {
    -webkit-transform: translate(50%, -50%) scale(2);
            transform: translate(50%, -50%) scale(2);
    opacity: 0.85; }
  100% {
    -webkit-transform: translate(50%, -50%) scale(4.5);
            transform: translate(50%, -50%) scale(4.5);
    opacity: 0; } }

.c-ripple {
  position: relative; }

.c-ripple__ripple {
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid white;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  opacity: 0; }

.c-ripple--will-change > .c-ripple__ripple {
  will-change: transform, opacity; }

.c-ripple--active > .c-ripple__ripple {
  -webkit-animation: ripple-effect 0.45s linear 0s 1 normal both;
          animation: ripple-effect 0.45s linear 0s 1 normal both; }



